@media (max-width: 991px) {
  .btn-menu {
    width: 30px;
    height: 30px;
    display: block;
    position: relative;
  }

  .menu__btn-close {
    display: none;
  }

  .nav__list {
    flex-direction: column;
    gap: 20px;
    display: flex;
  }

  .btn-menu__icon, .btn-menu__icon:before, .btn-menu__icon:after {
    content: "";
    width: 100%;
    height: 2px;
    background-color: #8f95a5;
    transition: top .1s ease-in;
    position: absolute;
    left: 0;
  }

  .btn-menu__icon {
    top: calc(50% - 1px);
  }

  .btn-menu__icon:after {
    width: 60%;
    top: 8px;
  }

  .btn-menu__icon:before {
    top: -8px;
  }

  .menu-open .btn-menu__icon:before, .menu-open .btn-menu__icon:after {
    width: 100%;
    top: 0;
  }

  .menu-open .btn-menu__icon {
    transform: rotate(-45deg);
  }

  .menu-open .btn-menu__icon:after {
    transform: rotate(90deg);
  }

  .header__menu {
    width: 100%;
    height: 100%;
    padding-top: 130px;
    background-color: #fff;
    flex-direction: column;
    align-items: flex-start;
    row-gap: 60px;
    padding-left: 12px;
    padding-right: 12px;
    transition: transform .3s ease-in;
    display: flex;
    position: fixed;
    top: 0;
    left: 0;
    transform: translateX(-100%);
  }

  .header__menu:before {
    content: "";
    height: 2px;
    width: 98%;
    background-color: #c4c4c4;
    position: absolute;
    top: 90px;
    left: 50%;
    transform: translateX(-50%);
  }

  .menu-open .header__menu {
    transform: translateX(0);
  }

  .nav__link {
    color: #263238;
    color: #263238;
    font-size: 22px;
    font-weight: 500;
  }
}

@media (max-width: 567px) {
  .header__menu:before {
    width: 95%;
  }
}

